import * as React from "react";
import { Jumbotron } from 'react-bootstrap'

const Title = () => {
  return (
        <Jumbotron>
            <h1>Open-Journal</h1>
            <p>open source peer reviews - powered by etherium</p>
        </Jumbotron>
  )
}

export default Title



